.calmdown-default-theme {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: white;
  border: 1px solid #84A98C; }
  .calmdown-default-theme .cd-editor {
    flex: 1 1 50%;
    max-width: 50%;
    padding: 10px;
    border: 0;
    border-right: 1px solid #84A98C;
    resize: none; }
    .calmdown-default-theme .cd-editor:focus {
      outline: 0; }
  .calmdown-default-theme .cd-preview {
    flex: 1 1 50%;
    max-width: 50%;
    padding: 10px;
    word-wrap: break-word;
    overflow-y: scroll;
    overflow-x: hidden; }
  .calmdown-default-theme .cd-html-input,
  .calmdown-default-theme .cd-markdown-input {
    display: none; }

.cd-highlight-default .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #23241f; }

.cd-highlight-default .hljs,
.cd-highlight-default .hljs-tag,
.cd-highlight-default .hljs-subst {
  color: #f8f8f2; }

.cd-highlight-default .hljs-strong,
.cd-highlight-default .hljs-emphasis {
  color: #a8a8a2; }

.cd-highlight-default .hljs-bullet,
.cd-highlight-default .hljs-quote,
.cd-highlight-default .hljs-number,
.cd-highlight-default .hljs-regexp,
.cd-highlight-default .hljs-literal,
.cd-highlight-default .hljs-link {
  color: #ae81ff; }

.cd-highlight-default .hljs-code,
.cd-highlight-default .hljs-title,
.cd-highlight-default .hljs-section,
.cd-highlight-default .hljs-selector-class {
  color: #a6e22e; }

.cd-highlight-default .hljs-strong {
  font-weight: bold; }

.cd-highlight-default .hljs-emphasis {
  font-style: italic; }

.cd-highlight-default .hljs-keyword,
.cd-highlight-default .hljs-selector-tag,
.cd-highlight-default .hljs-name,
.cd-highlight-default .hljs-attr {
  color: #f92672; }

.cd-highlight-default .hljs-symbol,
.cd-highlight-default .hljs-attribute {
  color: #66d9ef; }

.cd-highlight-default .hljs-params,
.cd-highlight-default .hljs-class .hljs-title {
  color: #f8f8f2; }

.cd-highlight-default .hljs-string,
.cd-highlight-default .hljs-type,
.cd-highlight-default .hljs-built_in,
.cd-highlight-default .hljs-builtin-name,
.cd-highlight-default .hljs-selector-id,
.cd-highlight-default .hljs-selector-attr,
.cd-highlight-default .hljs-selector-pseudo,
.cd-highlight-default .hljs-addition,
.cd-highlight-default .hljs-variable,
.cd-highlight-default .hljs-template-variable {
  color: #e6db74; }

.cd-highlight-default .hljs-comment,
.cd-highlight-default .hljs-deletion,
.cd-highlight-default .hljs-meta {
  color: #75715e; }
